body {
  height: 100vh;
  font-family: 'Open Sans Regular', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.63;
  background-color: var(--bg-color-white);
  color: var(--text-color-dark);
  margin: 0;
  @include mq(tab) {
    font-size: 18px;
    line-height: 1.56;
  }
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 1 auto;
}
