.hero {
  padding-top: 142px;
  padding-bottom: 96px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url('../images/home/hero-mob.jpg');
  @include mq(retina) {
    background-image: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.5),
        rgba(0, 0, 0, 0.5)
      ),
      url('../images/home/hero-mob@2x.jpg');
  }
  @include mq(tab) {
    padding-top: 407px;
    padding-bottom: 410px;
    @include mq(retina) {
      background-image: linear-gradient(
          0deg,
          rgba(0, 0, 0, 0.5),
          rgba(0, 0, 0, 0.5)
        ),
        url('../images/home/hero-mob@2x.jpg');
    }
  }
  @include mq(desk) {
    padding-top: 279px;
    padding-bottom: 282px;
    @include mq(retina) {
      background-image: linear-gradient(
          0deg,
          rgba(0, 0, 0, 0.5),
          rgba(0, 0, 0, 0.5)
        ),
        url('../images/home/hero-mob@2x.jpg');
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__inner {
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    font-weight: 300;
    font-size: 52px;
    line-height: 66px;
    color: var(--text-hero);

    @include mq(tab) {
      flex-direction: row;
      margin-bottom: 20px;
    }

    &--first {
      @include mq(tab) {
        margin-right: 16px;
      }
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
    color: var(--text-hero);
    @include mq(tab) {
      flex-direction: row;
      margin-bottom: 24px;
    }
    &--first {
      @include mq(tab) {
        margin-right: 6px;
      }
    }

    &--second {
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 156px;
    height: 45px;
    border: none;
    border-radius: 5px;

    font-size: 18px;
    color: var(--text-color-white);
    background-color: var(--main-grean-color);
  }
  .button__icon {
    display: none;
    @include mq(tab) {
      display: block;
      margin-right: 5px;
    }
  }
}
